import PropTypes, {arrayOf} from 'prop-types'
import React from 'react';
import "./article-tags.scss";

function ArticleTags({ tags }) {
  const tagElements = tags.map((tag, i) => <div key={i} className="article-tags__item"><span>{tag}</span></div>);

  return (
    <div className="article-tags">
      {tagElements}
    </div>
  )
}

ArticleTags.propTypes = {
  tags: arrayOf(PropTypes.string).isRequired,
};

export default ArticleTags;
