import React from 'react'
import {arrayOf, string, shape} from 'prop-types'
import {Title, TitleAccent} from "../../components/Title";
import {Grid, GridColumn, GridContainer} from "../../components/Grid";
import ArticleTags from "../../components/ArticleTags";
import Cta from "../../components/Cta";
import "./news-article.scss";
import Divider from "../../components/Divider";

const renderRelatedElement = (article) => {
  return (
    <div className="news-article__related-content">

      <Title variant="cardTitleSmall" className="news-article__related-title">
        {article.title}
      </Title>

      <Cta to={article.url} variant="readmore-yellow">
        read more
      </Cta>
    </div>
  );
};

function NewsArticle({article}) {
  const relatedArticles = article.related.map((relatedArticle) => renderRelatedElement(relatedArticle));

  return (
    <GridContainer className="news-article">
      <Title variant="lightLead" className="news-article__title">
        {article.title}
      </Title>

      <GridContainer className="news-article__wrapper">
        <Grid variant="centered">
          <GridColumn columns="12">
            <Grid>
              <GridColumn className="news-article__sidebar" columns="4">
                <div className="news-article__sidebar-date">
                  {article.date}
                </div>

                <div className="news-article__sidebar-author">
                  WRITTEN BY <br/>
                  {article.author}
                </div>

                <ArticleTags tags={article.articleTags}/>

              </GridColumn>

              <GridColumn columns="8" className="news-article__content">
                {article.body}
              </GridColumn>
            </Grid>
          </GridColumn>
        </Grid>
      </GridContainer>

      <Divider variant="completeGray"/>

      <GridContainer className="news-article__related">

        <Grid>
          <GridColumn columns="6" className="news-article__related-left">
            <Title variant="lightLead" className="news-article__related-main-title">
              Related <br/>
              <TitleAccent>articles</TitleAccent>
            </Title>
          </GridColumn>

          <GridColumn columns="6" className="news-article__related-right">
            {relatedArticles}
          </GridColumn>
        </Grid>

      </GridContainer>

    </GridContainer>
  )
}

NewsArticle.propTypes = {
  article: shape({
    title: string.isRequired,
    body: string.isRequired,
    date: string.isRequired,
    author: string.isRequired,
    articleTags: arrayOf(string).isRequired,
    related: arrayOf(shape({
      title: string.isRequired,
      url: string.isRequired,
    })),
  }).isRequired,
};

export default NewsArticle
